import CryptoJS from 'crypto-js'
//
const vi = '01234567'
//key
const keys = {
  header: '*|rX<xO7<6B}7PA])?dN8fgX}wOr\'HQh',
  pwd: 'cjm\'|I-pE2[8=TM4[1PXRu,_xiE}j0X>px2_MqOSU=%;/HK<lB'
}

const encryptByDes = (text, by = 'pwd') => {
  const encrypt = CryptoJS.TripleDES.encrypt(
    text, // 
    CryptoJS.enc.Utf8.parse(keys[by]), // //key240(0),24,24,
    { //config
      iv: CryptoJS.enc.Utf8.parse(vi), //iv
      mode: CryptoJS.mode.CBC, //
      padding: CryptoJS.pad.Pkcs7 //padding
    })
  return encrypt.toString().replace(/\s+/g,"").trim() //
}

const decryptByDes = (ciphertext, by = 'pwd') => {
  const decrypted = CryptoJS.TripleDES.decrypt({
      ciphertext: CryptoJS.enc.Base64.parse(ciphertext.replace(/\s+/g, ''))
    },
    // ciphertext,
    CryptoJS.enc.Utf8.parse(keys[by]), {
      iv: CryptoJS.enc.Utf8.parse(vi),
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    })
  return decrypted.toString(CryptoJS.enc.Utf8)
}

const sha384 = CryptoJS.SHA384

// 123456 -->  Zp2+Yksjxl8=

export {
  encryptByDes,
  decryptByDes,
  sha384
}
