import React from 'react'
import {Flex} from 'antd-mobile'
import './index.scss'
import {Dialog} from 'utils'

class BuyGoodsBottom extends React.Component {
    // 
    constructor(props: any) {
        super(props);
        // 
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        return (
            <Flex className={'buy_goods_bottom'} onClick={Dialog.showDownloadModal}>
                <Flex.Item className={'oprs bd_u'}>
                    <div className={'item'}>
                        <img src="/lsc_h5_hash/imgs/servise.png" alt="" />
                        <div></div>
                    </div>
                    <div className={'item'}>
                        <img src="/lsc_h5_hash/imgs/shop.png" alt="" />
                        <div></div>
                    </div>
                    <div className={'item'}>
                        <img src="/lsc_h5_hash/imgs/buy_car.png" alt="" />
                        <div></div>
                    </div>
                </Flex.Item>
                <div className={'opr state0 '}></div>
                <div className={'opr state1 '}></div>
            </Flex>
        );
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default BuyGoodsBottom
