export function commonReducer(
    state = initState,
    action: ActionType
) {
    const {
        type,
        payload = {}
    } = action
    const {
        cities,
        index,
        list = [],
        totalPage,
        storeInfo,
        userInfo
    } = payload
    const {
        comments,
        showModal
    } = state
    switch (type) {
        case Types.selectTabBar:
            state.tabBarList = state.tabBarList.map((e, i) => {
                e.isSelected = i === index
                return e
            })
            break;
        case Types.getGoodsCategories:
            state.goodsCategories = list
            break;
        case Types.getPaginationData:
            state.totalPage = totalPage
            break;
        case Types.changeCommonData:
            Object.assign(state, {...payload})
            break;
        case Types.getStoreInfo:
            state.storeInfo = {...storeInfo}
            break;
        case Types.toggleModal:
            state.showModal = !showModal
            break;
        case Types.getCities:
            // @ts-ignore
            state.pacArr = [...list]
            break;
        case Types.getSubCities:
            let pIndex, cIndex
            pIndex = state.pacArr.findIndex((e: any) => e.number === cities[0])
            if (cities.length === 1) {
                // @ts-ignore
                state.pacArr[pIndex].children = list
            }
            if (cities.length === 2) {
                // @ts-ignore
                cIndex = state.pacArr[pIndex].children.findIndex((e: any) => e.number === cities[1])
                // @ts-ignore
                state.pacArr[pIndex].children[cIndex].children = list
            }
            state.pacArr = [...state.pacArr]
            break;
        case Types.loginSuccess:
            Object.assign(state.userInfo, userInfo)
            break;
        case Types.getComments:
            // @ts-ignore
            state.comments = [...comments, ...list]
            break;
        default:
            break;
    }
    return {...state}
}


