import {Toast} from 'antd-mobile';
import {localDB, Utils, Dialog, WxConfig} from 'utils'
import {commonActions} from 'action'

/*
* 
method:  GETPOST
headers:  Headers  ByteString 
body:  body  BlobBufferSourceFormDataURLSearchParams  USVString  GET  HEAD  body 
mode:  cors no-cors  same-origin
credentials:  credentials omitsame-origin  include cookie    Chrome 50   FederatedCredential  PasswordCredential 
cache:   cache : default  no-store  reload  no-cache  force-cache  only-if-cached 
redirect:  redirect : follow (), error (),  manual (). ChromeChrome 47 follow Chrome 47 manual
referrer:  USVString  no-referrerclient URL client
referrerPolicy: Specifies the value of the referer HTTP header. May be one of no-referrer no-referrer-when-downgrade origin origin-when-cross-origin unsafe-url 
integrity:   subresource integrity    sha256-BpfBw7ivV8q2jLiT13fxDYAe2tJllusRSZ273h2nFSE=
*
  "proxy": {
      "/api/*":  {
        "target": "https://ts.vson.com.cn:26082",
        "secure": false
      }
    }
*
* */

export interface FetchOptions {
    method?: string,
    headers?: HeadersInit,
    body?: any,
    mode?: any,
    credentials?: any,
    cache?: any,
    env?: string,
    domain?: string
}

const Hosts: any = {
    dev: 'https://ts.vson.com.cn:26082',
    pro: 'https://apis.myvsoncloud.com'
}
const domains: any = {
    m: '/vc-lscmas-ws-1.0.2/api/m',
    all: '/vc-lsc-ws-1.0.2/api/all',
    com: '/vc-css-ws-1.0.2/api/all',
    pm: '/vc-pms-ws-1.0.2/api/all',
    air: '/vc-air-ws-1.0.2/api/air',
    lxt: '/vc-ccs-ws-1.0.2/api/all'
}

const commonHeaders: any = {
    ost: 2,
    apv: '1.0',
    apt: 107
}

const contentTypes = [
    "application/x-www-form-urlencoded",
    'application/json;charset=UTF-8',
    "multipart/form-data"
]

export const Fetch = async (url: string, data: any = {}, options: any = {}) => {
    const {
        method = 'POST',
        headers = {},
        contentType = 0,
        mode = "cors",
        credentials = 'include',
        cache = "default",
        env = 'dev',
        domain = 'all',
        needLogin = false,
        doStatic = false
    } = options
    const {tk} = JSON.parse(localDB.DBGetter('userInfo') || "{}")
    if (!!tk) {
        commonHeaders.tk = tk
    } else if (needLogin) {
        if (!doStatic) {
            Dialog.Confirm({
                msg: '',
                onConfirm: commonActions.wxOAuth2
            })
        }
        return {}
    }
    if (url.indexOf('http') !== 0) {
        // url = Hosts[env] + domains[domain] + url
        // url = '/lsc_h5_hash/lsc_h5_api' + domains[domain] + url
        // url = '/lsc_h5' + domains[domain] + url
        url = domains[domain] + url
    }
    const params = Utils.getFetchParams(data || {}, contentType)
    let result: any = {}
    try {
        await fetch(url, {
            method,
            headers: {
                ...commonHeaders,
                "content-type": contentTypes[contentType],
                ...headers
            },
            body: params,
            mode,
            credentials,
            cache
        })
            .then(async res => {
                const {ok, statusText} = res
                if (ok) {
                    return await res.json()
                } else {
                    Toast.fail(statusText)
                    return {}
                }
            }, async err => {
                Toast.fail(err)
            })
            .then((response: any) => {
                const {retCode, result: res = {isOk: true}} = response
                if (retCode === '0') {
                    result = {...res, isOk: true}
                    return result
                } else if (retCode === '1003') {
                    Dialog.Confirm({
                        msg: "",
                        onConfirm: WxConfig.getWxCode,
                        onCancel: () => {
                            localDB.DBRemove('userInfo')
                        }
                    })
                    return {isOk: false}
                } else {
                    Toast.fail(JSON.stringify(res))
                    return {isOk: false}
                }
            }, async err => {
                Toast.fail(err)
            })
            .catch(error => {
                throw (error)
            })
    } catch (e) {
        Toast.fail('!');
        throw (e)
    }

    return result
}

