export const getWxCode = () => {
    /*
       href: "http://apis.myvsoncloud.com/build/#/mall/auction_goods_detail/53c398273ddf40f0ace5b8bb375dddc1"
       hash: "#/mall/auction_goods_detail/53c398273ddf40f0ace5b8bb375dddc1"
       origin: "http://apis.myvsoncloud.com"
       pathname: "/build/"
       hosthostname: "apis.myvsoncloud.com"
   * */
    const {search, href, origin, pathname} = window.location
    const oauth2Host = 'https://open.weixin.qq.com/connect/oauth2/authorize?'
    const obj = {
        appid: 'wx22dc4d474b52a4b2',
        redirect_uri: origin + pathname + '#' + href.split('#')[1],
        response_type: 'code',
        scope: 'snsapi_userinfo', // snsapi_base  snsapi_userinfo
        state: 'authorized',
        connect_redirect: '1#wechat_redirect'
    }
    const getCodeUrl = oauth2Host + Utils.spliceParams(obj)
    window.location.replace(getCodeUrl)
    // window.location.href = oauth2Host + Utils.spliceParams(obj)
    return getCodeUrl
}

/**
 * 
 * IOSdocument.title 
 * */
export const setWxTitle = (title: string) => {
    document.title = title;
    const iframe: any = document.createElement("iframe");
    iframe.setAttribute("src", "https://apis.myvsoncloud.com/lsc_h5_hash/imgs/logo.png");
    iframe.setAttribute("style", "display:none");
    iframe.addEventListener('load', function () {
        setTimeout(function () {
            try {
                iframe.removeEventListener('load');
            } catch (err) {
            }
            document.body.removeChild(iframe);
        }, 0);
    });
    document.body.appendChild(iframe);
};
